@import "../../scss/var.scss";
.Cardlist{
    
    cursor: none;
    &[data-dark=true]{
        color: $grey;
        h1{color: $grey;}
        .main-screen{background: $dark-grey;}
        .description{background: $black; color: $grey;}
        .card-wrapper{opacity: 0.8;}
        .loader{background: black; -webkit-text-stroke: 2px white;}
    }
    width: 100vw;
    font-family: 'BlagovestFourSerifC';
    background: $white;
    overflow-x: hidden;
    .main-screen{
        width: 100vw;
        height: 90vh;
        background: $yellow;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        position: relative;
        overflow: hidden;
        h1{
            @include adaptiv-font(60, 30);
            position: absolute;
            bottom: 3vh;
            left: 2vw;
            transform: rotate(-1deg);
            &::after{
                content: "";
                width: 100%;
                height: calc(100% + 10px);
                border-bottom: 1vh solid $white;
                position: absolute;
                top: 0;
                left: -20%;
        
            }
            &::before{
                content: "";
                width: 100%;
                height: calc(100% + 10px);
                border-top: 1vh solid $white;
                position: absolute;
                bottom: 0;
                left: 20%;
            }
        }
        &__card-number{
            width: 100%;
            font-size: 36px;
            text-align: center;
            margin-bottom: 3vh;
        }
        .card-wrapper{
            height: 75.57446808510639vh;
            width: 23vw;

            border-radius: 20px;
            //overflow: hidden;
            margin-bottom: 10vh;
            @media (max-width:767px) {
                width: 80%;
            }
            perspective: 200px;
            .card{
                &:hover{
                    transform: rotateY(2deg);
                }
                transition-duration: 300ms;
            }
        }

        .button-container{
            width: 90%;
            display: flex;
            justify-content: space-between;
            height: 15%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            @media (max-width:767px) {
                top: 1vh;
                transform: translate(-50%);
                height: 7.5%;
            }
            &__next-button{
                height: 100%;
                
            }
            &__prev-button{
                height: 100%;
                transform: rotate(180deg);
            }
            ._unactive{
                fill: grey;
                opacity: 0.7;
            }
        }

    }
    .description{
        font-family: 'BlagovestFourSerifC';
        width: 100vw;
        min-height: 50vh;
        padding: 3vh 1vw;

        p{    
            color: black;
            font-size: 20px;
            &::first-letter{
                //color: red;
                text-transform: uppercase;
            }
        }
        h2{
            width: auto;
            font-weight: bold;
            font-size: 21px;
            overflow: hidden;
            &::first-letter{
                color: red;
                text-transform: uppercase;
            }
        }
    }
    .loader{
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        font-size: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: white;
        color: transparent;
        -webkit-text-stroke: 2px rgb(0, 0, 0);
        z-index: 1000;
        animation: 550ms 500ms opacity 1 forwards;
        &._hidden{
            opacity: 0;
            visibility: hidden;
        }
        //overflow: hidden;
        h2{
            
            font-family: 'BlagovestFourSerifC';
            
            position: relative;
            @include adaptiv-font(60, 45);
            &::after{
                content: "";
                width: 100%;
                height: calc(100% + 10px);
                border-bottom: 1vh solid $yellow;
                position: absolute;
                top: 0;
                left: -10%;
                transform: translate(-100%);
                animation: 500ms loader 1 forwards;
        
            }
            &::before{
                content: "";
                width: 100%;
                height: calc(100% + 10px);
                border-top: 1vh solid $yellow;
                position: absolute;
                bottom: 0;
                left: 10%;
                transform: translate(100%);
                animation: 500ms loader 1 forwards;
            }
        }
    }
}

@keyframes loader {
    0%{

    }
    100%{
        transform: none;
        //font-size: 200px;
    }
}
@keyframes opacity {
    0%{

    }
    99%{opacity: 0;}
    100%{
        opacity: 0;
        visibility: hidden;
        //font-size: 200px;
    }
}
