@import '../../scss/var.scss';
.card {
    font-family: 'BlagovestFourSerifC';
    width: 100%;
    height: 100%;
    background: $beige;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    overflow: hidden;
    border-radius: 20px;
    
    &__title{
        font-family: 'BlagovestFourSerifC';
        width: 100%;
        height: 13%;
        background: $red;
        color: $beige;
        text-transform: uppercase;
        display: flex;
        justify-content: center;
        align-items: center;
        white-space: nowrap;
        @include adaptiv-font(20, 10);
    }
    &__img{
        margin: 1vh 0;
        width: 100%;
        height: 50%;
        background: center/cover;

    }
    &__description{
        margin: 0 1vw;
        color: $black;
        text-align: center;
        margin-top: 3vh;
        height: 30%;
        width: 100%;
        font-family: 'BlagovestFourSerifC';
        font-weight: normal;
        @include adaptiv-font(20, 14);
    }
    &__state{
        align-self: flex-start;
        padding: 0.5vh 1vw;
        display: flex;
        justify-content: space-between;
        width: 100%;
        text-transform: lowercase;
    }
}