//?Цветовая палитра
$black:rgba(0, 0, 0, 0.829);
$white:#fffff0;
$yellow:rgba(255, 185, 35, 1);
$wenge:rgb(100, 84, 82);
$beige:rgb(255, 242, 229);
$red:#ea1c26;
$grey: rgb(109, 109, 109);
$white-grad: linear-gradient(45deg, #f1a470, #6dc1e7);
$dark-grey: rgb(34, 34, 34);
//?Переменные
$maxWidth: 1280;

//?Миксины
@mixin adaptiv-font($pcSize, $mobSize) {
    $calcSize: $pcSize - $mobSize;
    $maxWidth: $maxWidth - 320;
    font-size: calc(#{$mobSize + px} + #{$calcSize} * ((100vw - 320px) / #{$maxWidth}));
}